.flex-break-column {
  flex-basis: 100%;
  width: 0;
}

select.select2 {
  opacity: 0;
}

.btn {
  display: inline-block;
  padding: 8px;
  border: 1px solid #a6b3ce;
  border-radius: 4px;

  &-primary {
    min-width: 110px;
    background: #f5f7fa;
    box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.15);
    border-radius: 6px;
    color: #8c99b4;
    text-align: center;
    padding: 14px;

    &.active {
      background: $primary;
      color: #fff;
    }
  }
}

.icm-icon-chevron-right {
  &--left {
    &:before {
      display: inline-block;
      transform: rotate(180deg);
    }
  }
}

.sticky {
  position: sticky;
  top: 0;
  z-index: $zindex-sticky;
}

.form-selector-custom {
  position: relative;

  &__value {
    user-select: none;
    cursor: pointer;
  }

  &__dropdown {
    display: none;
    position: absolute;

    &-item {
      cursor: pointer;
    }
  }

  input {
    display: none;
  }

  input:checked ~ .form-selector-custom__dropdown {
    display: block;
  }
}

.details-select {
  user-select: none;

  summary {
    list-style-type: none;
    border: 1px solid #e5e5e5;
    border-radius: 4px;
    color: #616161;
    position: relative;
    padding: 12px;
    cursor: pointer;

    &::-webkit-details-marker,
    &::marker {
      display: none;
    }

    &:after {
      font-family: 'icomoon';
      content: '\e919';
      position: absolute;
      right: 10px;
      top: calc(50% - 7px);
    }
  }

  &__dropdown {
    background: #ffffff;
    box-shadow: 0px 3px 10px 3px rgba(0, 0, 0, 0.15);
    border-radius: 0px 0px 6px 6px;
    padding: 8px;

    &-item {
      padding: 12px;
      border-radius: 4px;
      cursor: pointer;

      &:hover {
        background: #f5f7fa;
      }
    }
  }

  &[open] {
    summary {
      &:after {
        transform: rotate(180deg);
      }
    }
  }
}

.color-link {
  color: #0887e2;
}

.btn-primary--chart {
  display: inline-flex;
  align-items: center;
  background: $primary;
  color: #fff;
  width: 100px;
  height: 32px;
  font-weight: 700;
  font-size: 13px;
  padding: 0px 10px;
  position: relative;

  &.btn-primary--hidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
  }

  &:before {
    content: 'Thu gọn';
    margin-right: 10px;
  }

  &:after {
    font-family: 'icomoon';
    content: '\e905';
    transform: rotate(180deg) translateY(50%);
    display: inline-block;
    margin-left: auto;
    font-weight: 400;
    position: absolute;
    top: 50%;
    right: 10px;
  }

  &.active {
    background: #f5f7fa;
    color: #8c99b4;

    &:hover {
      background: #ebeef4;
    }
  }

  &:not(.active) {
    &:before {
      content: 'Theo năm';
    }

    &:after {
      content: '\e907';
      transform: rotate(0) translateY(-50%);
      font-size: 20px;
      right: 7px;
    }

    &:hover {
      background: #e59e00;
    }
  }
}

* + .btn-primary--chart {
  margin-left: 3px;
}

// Tooltip

[class*='tooltip'] {
  position: relative;

  &:after {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    content: attr(data-tooltip);
    padding: 4px 5px;
    top: 1.4em;
    left: 50%;
    transform: translateX(-50%) translateY(-2px);
    background: rgba(0, 0, 0, 0.7);
    border-radius: 6px;
    color: white;
    font-size: 12px;
    white-space: nowrap;
    z-index: 2;
    transition: opacity 0.2s $cubic, transform 0.2s $cubic;
  }

  &:hover {
    &:after {
      display: block;
      opacity: 1;
      visibility: visible;
      transform: translateX(-50%) translateY(0);
    }
  }
}

.tooltip--top {
  &:after {
    top: 0;
  }
  &:hover {
    &:after {
      transform: translateX(-50%) translateY(-110%);
    }
  }
}

.tooltip--left {
  &:after {
    top: -4px;
    left: 0;
    transform: translateX(-112%) translateY(0);
  }

  &:hover {
    &:after {
      transform: translateX(-110%) translateY(0);
    }
  }
}

.tooltip--right {
  &:after {
    top: -4px;
    left: 100%;
    transform: translateX(12%) translateY(0);
  }

  &:hover {
    &:after {
      transform: translateX(10%) translateY(0);
    }
  }
}

.tooltip--triangle {
  &:before {
    content: '';
    width: 0;
    height: 0;
    border-left: solid 5px transparent;
    border-right: solid 5px transparent;
    border-bottom: solid 5px grey;
    opacity: 0;
    visibility: hidden;
    position: absolute;
    transform: translateX(-50%) translateY(-2px);
    top: 1.1em;
    left: 50%;
    transition: opacity 0.2s $cubic, transform 0.2s $cubic;
    z-index: 3;
  }

  &:hover {
    &:before {
      display: block;
      opacity: 1;
      visibility: visible;
      transform: translateX(-50%) translateY(0);
    }
  }
}

%custom-scrollbar {
  scrollbar-width: thin;
  scrollbar-color: grey transparent;

  &::-webkit-scrollbar {
    width: 5px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #ddd
  }
}

%hide-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }
}

.icm-icon-descending-sorting--rotate-180 {
  transform: rotate(180deg);
  display: inline-block;
  position: relative;
  top: 2px;
}