img,
svg,
iframe,
audio,
video {
    max-width: 100%;
}

a {
    text-decoration: none;
}

ul,
ol {
    list-style: none;
}

button {
    border: 0;
    background: transparent;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

textarea,
input[type=text],
input[type=number],
input[type=password] {
    appearance: none;

    &:focus {
        outline: none;
        border: 1px solid #e5e5e5;
    }
}


// Reset Vne Default

.section {
    float: none;
}

.top-header {
    background: #fff;
}

input[type=email], input[type=password], input[type=tel], input[type=text], select, textarea {
    border: 1px solid #E5E5E5;
    border-radius: 4px;
}