.main {
  padding-top: 15px;

  .container {
    display: flex;
    background: #fff;
    padding: 15px;
  }

  &__content {
    flex-grow: 1;
    max-width: 100%;
  }

  &__aside {
    width: 300px;
    flex-shrink: 0;
    margin-left: 15px;
  }

  @media (max-width: 991px) {
    &__aside {
      display: none;
    }
  }

  @media (max-width: 767px) {
    padding-top: 0;
  }
}

// Navigation
.navigation {
  z-index: 1001;
  background: #fff;
  padding: 15px 0;
  box-shadow: inset 0px -1px 0px #e5e5e5;
  transition: 0.3s all cubic-bezier(0.075, 0.82, 0.165, 1);

  .container {
    display: flex;
    align-items: center;
  }

  &__logo {
    margin-right: 20px;

    h1 {
      margin-bottom: 0;
    }
  }

  &__list {
    margin-left: auto;
  }

  &__items {
    margin-bottom: 0;
    display: flex;
    align-items: center;
    color: #5e6678;
    font-size: 12px;
  }

  &__item {
    text-transform: uppercase;

    + * {
      margin-left: 40px;
    }

    &.active {
      color: #1f2228;
      font-weight: 700;
    }

    &:hover {
      color: $primary;
    }
  }

  @media (max-width: 991px) {
    &__item {
      + * {
        margin-left: 20px;
      }
    }
  }

  @media (max-width: 767px) {
    &.sticky--active {
      &.down {
        position: relative;
      }

      &:not(.down) {
        animation-name: fadeInDown;
        animation-duration: 0.6s;
        animation-fill-mode: both;
      }
    }

    .btn-back {
      display: none;
    }

    &__item {
      &:not(.active) {
        display: none;
      }
    }
  }
}

.btn-back {
  display: flex;
  align-items: center;
  color: #7d88a0;
  font-size: 12px;
  height: 30px;

  i {
    margin-right: 5px;
  }

  &:hover {
    color: $primary;
    border-color: $primary;
  }
}

// Footer
.footer {
  .container {
    background: #2f333c;
    color: #fff;
    display: grid;
    grid-template-columns: 150px auto auto;
    column-gap: 70px;
    align-items: center;
    padding-top: 35px;
    padding-bottom: 35px;
  }

  &__vne {
    grid-row: 1 / span 2;
  }

  &__logo {
    display: flex;
    align-items: center;
    grid-row: 1 / span 2;

    &-link {
      margin-right: 15px;
    }

    .btn-outline {
      color: #f5f7fa;
      border: 1px solid;

      &:hover {
        color: $primary;
      }
    }
  }

  &__nav {
    font-size: 12px;
    margin-bottom: 10px;

    &-items {
      display: flex;
      justify-content: space-between;
    }

    &-item {
      text-transform: uppercase;

      + * {
        margin-left: 10px;
      }

      &:hover {
        color: $primary;
      }
    }
  }

  &__contact {
    font-size: 12px;
    display: flex;
    justify-content: space-between;

    &-item {
      display: flex;
      align-items: center;

      i {
        flex-shrink: 0;
        font-size: 16px;
        margin-right: 5px;
        font-weight: 400;
      }

      + * {
        margin-left: 10px;
      }
    }
  }

  @media (max-width: 991px) {
    .container {
      grid-template-columns: 40% auto;
      column-gap: 30px;
      row-gap: 20px;
    }

    &__vne {
      grid-row: unset;
      grid-column: 1 / span 2;
    }

    &__logo {
      grid-row: unset;
      grid-column: 1 / span 1;
    }

    &__contact {
      grid-column: 1 / span 2;
    }
  }

  @media (max-width: 767px) {
    font-size: 12px;

    &__contact {
      flex-direction: column;

      &-item {
        + * {
          margin-left: 0;
          margin-top: 35px;
        }
      }
    }

    &__logo {
      display: flex;
      flex-direction: column;
    }

    &__nav {
      margin-bottom: 0;

      &-items {
        flex-direction: column;
      }

      &-item {
        font-weight: 700;

        + * {
          margin-left: 0;
          margin-top: 20px;
        }
      }
    }
  }
}

@media (max-width: 767px) {
  .time-now {
    display: none;
  }

  .myvne_taskbar .log_txt {
    font-size: 0;
  }

  .top-header {
    z-index: $zindex-sticky + 1;
  }

  .top-header > .container {
    display: flex;
    justify-content: center;
  }

  .top-header > .container > .right {
    position: absolute;
    right: 15px;
    top: 5px;
    width: 32px;
    height: 32px;
  }
}

#to_top {
  width: 40px;
  height: 40px;
  background: #fff;
  border: 1px solid #e5e5e5;
  line-height: 40px;
  text-align: center;
  border-radius: 3px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  display: none;
}
